import React, {  useState, useEffect } from 'react';
import { Fade } from '@mui/material';

function MyEmote({ isAnswered, delay }) {
    const [show, setShow] = useState(false)

    useEffect(() => {
        const timeId = setTimeout(() => {
            setShow(true)
        }, delay)

        return () => {
            clearTimeout(timeId)
        }
    }, []);
    if (show && isAnswered) {
        return <Fade in={true}
            timeout={{ enter: 1000, exit: 250 }}
            style={{ transitionDelay: `100ms` }}>
            <div style={{
                margin: "0px",
                height: "100px",
            }}>
                <img src='https://raw.communitydragon.org/latest/game/assets/loadouts/summoneremotes/flairs/penguin_happy_greetings_selector.png'></img></div></Fade>
    }

    else
        return;
}

export default MyEmote;