import React, {  useState, useEffect } from 'react';
import Confetti from 'react-confetti'


function MyConfetti({ isAnswered, delay }) {
    const [show, setShow] = useState(false)
    const [timedOut, setTimedOut] = useState(false)
    const [recycle, setRecycle] = useState(true)
    useEffect(() => {
        const timeId = setTimeout(() => {
            setShow(true)
            setTimeout(() => {
                setRecycle(false)
            }, 10000)
        }, delay)

        return () => {
            clearTimeout(timeId)
        }
    }, []);
    if (show && isAnswered) {
        if (!timedOut) {
            setTimedOut(true)
        }
        return <Confetti tweenDuration={5000} recycle={recycle} numberOfPieces={200}style={{ zIndex: "3" }}></Confetti>
    }
    else
        return;
}

export default MyConfetti;