import React from 'react';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import IconButton from '@mui/material/IconButton';

function MyShare({ set, championsGuessed, getGuessColour, answer, date, isMobile, withSkill }) {

    if (withSkill) {
        return <div style={{ width: isMobile ? "360px" : "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(252, 204, 78, 0.8), rgb(249, 148, 65, 0.8))", border: "2px solid white", boxShadow: "inset 0 0 6px black" }}>
            <p style={{
                fontSize: "24px",
                fontFamily: "Roboto",
                color: "white",
                margin: "10px",
                textShadow: "0 0 3px black"
            }}> I have guessed todays Tftdle with ability for set {set == 9 ? '9' : '8.5'} in {championsGuessed.length} {championsGuessed.length == 1 ? "try" : "tries"}! <br/> https://www.tftdle.net</p>
            <IconButton onClick={() => { navigator.clipboard.writeText('I have guessed todays Tftdle #' + date + '  with ability for set ' + (set == 9 ? '9' : '8.5') + ' in ' + championsGuessed.length + (championsGuessed.length == 1 ? ' try.\n' : ' tries.\n') + 'Try it on: https://www.tftdle.net') }}
                variant="contained"
                style={{ marginTop: "auto", }} >
                <ContentCopyIcon />
            </IconButton>
        </div>
    }
    else {
        const green = "#28aa0a"
        const red = "#D4312B"
        const orange = "#f07e0f"

        const guessColours = championsGuessed.map(champ => getGuessColour(champ, answer))
        var i = 0
        const guessPattern = guessColours.map(guessColour => {
            i++
            return <div key={i}>
                {guessColour[0] == green ? "🟩 " : "🟥 "}
                {guessColour[1] == green ? "🟩 " : "🟥 "}
                {guessColour[2] == green ? "🟩 " : guessColour[2] == orange ? "🟧 " : "🟥 "}
                {guessColour[3] == green ? "🟩 " : guessColour[3] == orange ? "🟧 " : "🟥 "}
                {guessColour[4] == green ? "🟩 " : "🟥 "}
            </div>
        })
        var text = "";
        const textToCopy = guessColours.map(guessColour => {
            var text = ""
            text += (guessColour[0] == green ? "🟩  " : "🟥  ") + (guessColour[1] == green ? "🟩  " : "🟥  ") + (guessColour[2] == green ? "🟩  " : guessColour[2] == orange ? "🟧  " : "🟥  ") + (guessColour[3] == green ? "🟩  " : guessColour[3] == orange ? "🟧  " : "🟥  ") + (guessColour[4] == green ? "🟩 \n" : "🟥 \n")
            return text
        }).map(t => text += t)
        return <div style={{ width: isMobile ? "360px" : "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(252, 204, 78, 0.8), rgb(249, 148, 65, 0.8))", border: "2px solid white", boxShadow: "inset 0 0 6px black" }}>
            <p style={{
                fontSize: "20px",
                fontFamily: "Roboto",
                color: "white",
                width: "100%",
                textShadow: "0 0 3px black"
            }}> I have guessed todays Tftdle for set {set == 9 ? '9' : '8.5'} in {championsGuessed.length} {championsGuessed.length == 1 ? "try" : "tries"}.</p>
            {guessPattern}
            <IconButton onClick={() => { navigator.clipboard.writeText('I have guessed todays Tftdle #' + date + 'for set ' + (set == 9 ? '9' : '8.5') + ' in ' + championsGuessed.length + (championsGuessed.length == 1 ? ' try.\n' : ' tries.\n') + text + 'Try it on: https://www.tftdle.net') }}
                variant="contained"
                style={{ marginTop: "auto", }} >
                <ContentCopyIcon />
            </IconButton>
        </div>
    }
}

export default MyShare