import React, { Component, lazy } from 'react';
import { Table } from 'reactstrap';
import AppNavbar from './components/AppNavbar';
import './css/ChampionList.css';
import { Fade } from '@mui/material';
import ComboBox from './ComboBox';

import secureLocalStorage from "react-secure-storage";
import headersImage from "./img/tftdle-tableTitles.webp";
import { ColorRing } from 'react-loader-spinner'

import './css/Common.css';
import MyConfetti from './components/Confetti';
import MyEmote from './components/Emote';
import Solved from './components/Solved';
import MyShare from './components/Share';
import MyVictory from './components/Victory';
import {isMobileWidth, setWithExpiry, getWithExpiry} from './Common'
import Streak from './components/Streak';

class ChampionList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            champions: [],
            championsGuessed: [],
            answer: {},
            answerResult: false,
            showConfetti: false,
            hideComboBox: true,
            toggleInfoModal: false,
            numberOfPeopleSolved: 0,
            coloursGuessed: [],
            oldAnswer: {},
            date: 0,
            loading: true,
            isPhone: false,
            isMobile: false,
            width: 0,
            height: 0,
            setNumber: 0,
            streak: 0,
        };
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }


    componentDidUpdate() {
        setWithExpiry(this.state.setNumber + 'championsGuessed', this.state.championsGuessed, this.timeUntilMidnight())
        if (!this.isEmpty(this.state.answer) && !this.isEmpty(this.state.oldAnswer) && this.state.answer.name !== this.state.oldAnswer.name) {
            secureLocalStorage.clear()
            window.location.reload();
        }
    }

    componentDidMount() {


        var set = this.props.set;

        this.setState({ loading: true, setNumber: set })
        setTimeout(() => {
            this.setState({ loading: false });
        }, 500);

        var championsStored = false
        try {
            this.getStateFromLocalStorage(set + 'answerResult', 'answerResult')
            championsStored = this.getStateFromLocalStorage(set + 'champions', 'champions')
            this.getStateFromLocalStorage(set + 'championsGuessed', 'championsGuessed')
            this.getStateFromLocalStorage(set + 'answer', 'answer')
            this.getStateFromLocalStorage(set + 'hideComboBox', 'hideComboBox')
            this.getStateFromLocalStorage(set + 'streak', 'streak')
        } catch (e) { }

        if (!championsStored)
            fetch(`/all?set=${encodeURIComponent(set)}`)
                .then(response => response.json())
                .then(data => this.setState({ champions: data }));

        fetch(`/winner?set=${encodeURIComponent(set)}`)
            .then(response => response.json())
            .then(data => this.setState({ answer: data }))


        fetch(`/solved?set=${encodeURIComponent(set)}`)
            .then(response => response.json())
            .then(data => this.setState({ numberOfPeopleSolved: data }))

        fetch(`/date?set=${encodeURIComponent(set)}`)
            .then(response => response.json())
            .then(data => this.setState({ date: data }))

        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);


    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    getStateFromLocalStorage = (key, stateName) => {
        const state = getWithExpiry(key);
        if (state != null) {
            this.setState({ [stateName]: state });
            if (stateName == 'answer') {
                this.setState({ oldAnswer: state })
            }
            return true
        }
        return false
    }

    isEmpty(obj) {
        for (const prop in obj) {
            if (Object.hasOwn(obj, prop)) {
                return false;
            }
        }

        return true;
    }

    isValidChoice = (selectedOptions) => {
        if (selectedOptions == null)
            return false;
        const { championsGuessed } = this.state
        if (Object.keys(selectedOptions).length > 0) {
            if (championsGuessed.length == 0) {
                return true;
            }
            for (var champ of championsGuessed) {
                if (champ.name == selectedOptions.name)
                    return false
            }
            return true;
        }
        return false;
    }
    handleSubmit = (selectedOptions) => {

        if (this.isValidChoice(selectedOptions) && Object.keys(selectedOptions).length > 0) {
            var array1 = [...this.state.championsGuessed];
            array1.unshift(selectedOptions)
            this.setState(prevState => ({
                championsGuessed: [selectedOptions, ...prevState.championsGuessed]
            }))


            if (this.isCorrectAnswer(selectedOptions)) {
                this.setState({ answerResult: this.state.answerResult = true })
            }
            var array = [...this.state.champions]; 
            var index = array.indexOf(selectedOptions)
            if (index !== -1) {
                array.splice(index, 1);
                this.setState({ champions: array });
                setWithExpiry(this.state.setNumber + 'champions', array, this.timeUntilMidnight())
            }

        }
    }

    toggleInfoModal = () => {
        this.setState({ toggleInfoModal: !this.toggleInfoModal })
    }

    textBoxHandle = () => {
        this.setState({
            hideComboBox: false
        })
        setWithExpiry(this.state.setNumber + "hideComboBox", false, this.timeUntilMidnight())
    }


    timeUntilMidnight() {
        var midnight = new Date();
        midnight.setUTCHours(24, 0, 0, 0);
        return (midnight.getTime() - new Date().getTime());
    }

    isCorrectAnswer = (guess) => {
        const streak = this.state.streak
        setWithExpiry(this.state.setNumber + 'answer', this.state.answer, this.timeUntilMidnight())

        var count = 0;
        if (this.state.answer.gender == guess.gender) {
            count++;
        }
        if (this.state.answer.cost == guess.cost) {

            count++;
        }
        if (this.state.answer.originTrait == guess.originTrait) {

            count++;
        }
        if (this.state.answer.classTrait == guess.classTrait) {
            count++;
        }
        if (this.state.answer.rangeType == guess.rangeType) {
            count++;
        }

        if (count == 5) {
            setTimeout(() => {
                this.textBoxHandle()
            }, 6000)
            setWithExpiry(this.state.setNumber + 'answerResult', true, this.timeUntilMidnight())
            if (this.answerResult != true) {
                setWithExpiry(this.state.setNumber + 'streak', streak+1, this.timeUntilMidnight() + 86400000)
                this.setState(prevState => {
                    return {streak: prevState.streak + 1}
                 })
                var set = this.props.set;
                fetch(`/solve?set=${encodeURIComponent(set)}`)
                    .then(response => response.json())
                setTimeout(() => {
                    fetch(`/solved?set=${encodeURIComponent(set)}`)
                        .then(response => response.json())
                        .then(data => this.setState({ numberOfPeopleSolved: data }))
                }, 6000)

            }
            return true;
        }
        return false;
    }
    getGuessColour = (guess, answer) => {
        const green = "#28aa0a"
        const red = "#D4312B"
        const orange = "#f07e0f"

        var result = [];
        if (answer.gender == guess.gender) {
            result.push(green)
        }
        else
            result.push(red);
        if (answer.cost == guess.cost) {
            result.push(green)
        }
        else
            result.push(red);

        if (answer.originTrait == guess.originTrait) {
            result.push(green)

        }
        else {
            var colour = red;
            var ans = answer.originTrait.replace(/ /g, '');
            var ansArr = ans.split(",")

            var guess1 = guess.originTrait.replace(/ /g, '');
            var guessArr = guess1.split(",")

            for (var word1 of ansArr) {
                for (var word2 of guessArr) {
                    if (word1 == word2) {
                        colour = orange
                    }
                }
            }

            result.push(colour);
        }
        if (answer.classTrait == guess.classTrait) {
            result.push(green)
        }
        else {
            var colour = red;
            var ans = answer.classTrait.replace(/ /g, '');
            var ansArr = ans.split(",")

            var guess1 = guess.classTrait.replace(/ /g, '');
            var guessArr = guess1.split(",")

            for (var word1 of ansArr) {
                for (var word2 of guessArr) {
                    if (word1 == word2) {
                        colour = orange
                    }
                }
            }

            result.push(colour);
        }
        if (answer.rangeType == guess.rangeType) {
            result.push(green)
        }
        else
            result.push(red);

        return result;
    }

    render() {
        const { champions, championsGuessed, answer, answerResult, showConfetti, hideComboBox, numberOfPeopleSolved, coloursGuessed, date, loading, width, setNumber, streak } = this.state;
        const isMobile = isMobileWidth(width)


        const championList = championsGuessed.map(champion => {

            const guessColours = this.getGuessColour(champion, answer);
            return <tr key={champion.name}>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `100ms` }}>

                    <td style={{ border: "1px solid rgb(255, 255, 255, 0)", backgroundColor: "rgb(0, 0, 0, 0)", margin: "0 px", padding: "0px" }} >
                        <a title={champion.name}><img style={{ border: "solid 2px", borderRadius: "10px", boxShadow: "inset 0 0 6px black" }} src={champion.imgUrl} alt="some" height="80px"></img></a>
                    </td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `1100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[0], margin: "0 px", padding: "0px" }}>{champion.gender}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `2100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[1], margin: "0 px", padding: "0px" }}>{champion.cost}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `3100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[2], margin: "0 px", padding: "0px" }}>{champion.originTrait}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `4100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[3], margin: "0 px", padding: "0px" }}>{champion.classTrait}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `5100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[4], margin: "0 px", padding: "0px" }}>{champion.rangeType}</td>
                </Fade>
            </tr>
        });

        return (
            <div>
                <AppNavbar isMobile={isMobile} />
                {loading ? (
                    <div style={{ width: isMobile ? "355px" : "650px", margin: "auto" }} >
                        <ColorRing
                            visible={true}
                            height="80"
                            width="80"
                            ariaLabel="blocks-loading"
                            wrapperStyle={{}}
                            wrapperClass="blocks-wrapper"
                            colors={['#e15b64', '#f47e60', '#f8b26a', '#fccc4e', '#f99441']}
                        />
                    </div>
                ) : (
                    <div>
                        <div className={(answerResult ? 'show' : 'hidden')}>
                            {answerResult && <MyConfetti isAnswered={answerResult} handler={this.textBoxHandle} delay={6000} />}
                        </div>
                        <div>

                        </div>
                        <div style={{
                            // margin: "-20px",
                            height: "120px",
                            // border: "solid 10px"
                        }}>
                            {hideComboBox && <ComboBox isMobile={isMobile} champions={champions} handleSubmit={this.handleSubmit} />}
                            {answerResult && <MyEmote isAnswered={answerResult} delay={6000}/>}
                        </div>
                        <div className='pad' style={{
                            width: isMobile ? "360px" : "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(249, 148, 65, 0.8), rgb(252, 204, 78, 0.8))", border: "2px solid white", position: "relative"

                        }}>
                            {!isMobile && <img src={headersImage} style={{
                                width: isMobile ? "350px" : "642px",
                                height: isMobile ? "90px" : "50px",
                                marginLeft: isMobile ? "-175px" : "-320px",
                                marginTop: isMobile ? "-5px" : "0px",
                                padding: "0px",
                                position: "absolute",
                                zIndex: "1"
                            }}></img>}
                            <Table className="align-middle"
                                borderless
                                // hover
                                responsive
                                style={{
                                    position: "relative",
                                    borderCollapse: "separate",
                                    borderSpacing: "5px 5px",
                                    fontFamily: "Roboto",
                                    color: "white",
                                    width: "100%",
                                    textShadow: "0 0 3px black",
                                    zIndex: "2"
                                }}
                            >
                                <thead>
                                    <tr style={{ zIndex: "4" }}>
                                        <th style={{ width: "80px" }}>Champion</th>
                                        <th style={{ width: "80px" }}>Gender</th>
                                        <th style={{ width: "80px" }}>Cost</th>
                                        <th style={{ width: "80px" }}>Origin</th>
                                        <th style={{ width: "80px" }}>Class</th>
                                        <th style={{ width: "80px" }}>Range Type</th>
                                    </tr>
                                </thead>
                                <tbody >
                                    {championList}
                                </tbody>
                            </Table>
                        </div>

                        <div
                            style={{
                                fontSize: "20px",
                                fontFamily: "Roboto",
                                color: "white",
                                width: "100%",
                                textShadow: "0 0 3px black"
                            }}>
                            <Solved numberOfPeopleSolved={numberOfPeopleSolved} hideComboBox={hideComboBox} date={date}></Solved>
                        </div>
                        <Streak streak={streak}></Streak>
                        <div style={{
                            marginBottom: "10px"
                        }}>
                            {!hideComboBox && <MyVictory autoFocus coloursGuessed={coloursGuessed} championsGuessed={championsGuessed} getGuessColour={this.getGuessColour} answer={answer} date={date} isMobile={isMobile} timeUntilMidnight={this.timeUntilMidnight()}></MyVictory>}
                        </div>
                        <div style={{
                            marginBottom: "10px"
                        }}>
                            {!hideComboBox && <MyShare set={setNumber} coloursGuessed={coloursGuessed} championsGuessed={championsGuessed} getGuessColour={this.getGuessColour} answer={answer} date={date} isMobile={isMobile}></MyShare>}
                        </div>
                    </div>
                )}
            </div>
        );
    }
}


export default ChampionList;