import secureLocalStorage from "react-secure-storage";


    function setWithExpiry(key, value, interval) {
        const now = new Date();
        const item = {
            value: value,
            expiry: now.getTime() + interval,
        };
        secureLocalStorage.setItem(key, JSON.stringify(item));
    };

    function getWithExpiry(key) {
        const itemStr = secureLocalStorage.getItem(key)
        if (!itemStr) {
            return null;
        }
        const item = JSON.parse(itemStr);
        const now = new Date();
        if (now.getTime() > item.expiry) {
            secureLocalStorage.removeItem(key);
            return null;
        }
        return item.value;
    }

    function isMobileWidth(width) {
        return width < 750;
    }

    function isPhoneWidth(width) {
        return width < 420;
    }

    export {setWithExpiry, getWithExpiry, isMobileWidth, isPhoneWidth};