import { Component, React } from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { Box } from '@mui/material';
import Button from '@mui/material/Button';
import backgroundImage from "./img/tftdle-chooseyourchampion.webp";
import sendIcon from "./img/tftdle-send.webp";
import "./css/Common.css"


class ComboBox extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedOptions: []
        };
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange = (event, value) => {
        if (value !== undefined)
            this.setState({ selectedOptions: value });
    };

    handleSubmit = (e) => {
        
            this.props.handleSubmit(this.state.selectedOptions)
    }

    render() {
        // console.log(this.props.champions);
        const isMobile = this.props.isMobile;
        var champions = this.props.champions;
        const isLegend = this.props.isLegend;
        return (
            <div style={{
                marginTop: "0px",
                height: "100px",
                zIndex: "1",
                padding: "0px"
                // border: "2px solid",
            }}>
                <Autocomplete
                    className="customAutocomplete"
                    freeSolo={false}
                    isOptionEqualToValue={(option, value) => true}
                    sx={{
                        width: isMobile ? "192px" : "300px", margin: "0px", display: "inline-block", padding: "10px",

                        "& .MuiOutlinedInput-root": {
                            border: "none",
                        },
                        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
                            border: "none",
                        },

                        backgroundImage: `url(${backgroundImage})`,
                        backgroundPosition: 'center center',
                        backgroundSize: 'contain',
                        backgroundRepeat: 'no-repeat',
                    }}
                    options={champions}
                    autoHighlight
                    getOptionLabel={(option) => option.name}
                    onChange={this.handleChange}
                    renderOption={(props, option) => (
                        <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                            <img
                                loading="lazy"
                                width="40"
                                height="40"
                                src={isLegend? require(`${option.imgUrl}`):option.imgUrl}
                                srcSet={isLegend? require(`${option.imgUrl}`):option.imgUrl}
                                alt=""
                            />
                            {option.name}
                        </Box>
                    )}
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            // hiddenLabel
                            // id="filled-hidden-label-normal"
                            // variant="filled"
                            placeholder="Choose a champion..."
                            inputProps={{
                                ...params.inputProps,
                                // autoComplete: 'new-password', // disable autocomplete and autofill
                                onKeyDown: (e) => {
                                    if (e.key === 'Enter') {
                                        e.target.value = ''; // Clear the input value
                                        this.handleSubmit(e)
                                    }
                                },
                            }}
                        />
                    )}
                />
                <Button onClick={this.handleSubmit}
                    className="lightUpOnHover"
                    style={{
                        height: isMobile ? "40px" : "60px",
                        backgroundImage: `url(${sendIcon})`,
                        backgroundPosition: 'center center',
                        backgroundSize: isMobile ? 'contain' : 'cover',
                        backgroundRepeat: 'no-repeat',
                        marginTop: "30px", display: "inline-block", padding: "10px",
                    }}>
                    {/* <SendIcon/> */}
                </Button>
            </div>
        );

    }
}

export default ComboBox