
import React from 'react';

function Solved({ numberOfPeopleSolved, date }) {
    return <div>
        <span style={{ color: "yellow" }}>{numberOfPeopleSolved} </span> {numberOfPeopleSolved == 1 ? "person" : "people"} solved todays puzzle! <span style={{ color: "yellow" }}> #{date} </span>
    </div>
}

export default Solved;