import React, {Component, lazy, Suspense } from 'react';
import ChampionList from '../ChampionList';
import background from "../img/tft-wp.webp";
import ReactGA from "react-ga4";
import { getWithExpiry } from '../Common';
import Helmet from 'react-helmet';
import Ad from '../components/Ad';


const MyModal = lazy(() => import('../MyModal'));

class Set85 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            checked: false
        };
    }
    componentDidMount() {

        const cookiesEnabled = getWithExpiry("cookiesEnabled")
        if (cookiesEnabled != false) {
            ReactGA.initialize("G-QTDEZRHL1B");
            ReactGA.send({ hitType: "pageview", page: "/Set85", title: "Set85" })
        }
    }
    render() {
        const adEnabled = getWithExpiry("showAds") == null;

        return (
            <div>
                    <Helmet>
                        <meta charSet="utf-8" />
                        <title>Tftdle-Set8.5</title>
                        <meta name="description" content="TFTdle is a wordle like game for the popular game teamfight tactics! Try to guess the daily TFT champion!" />
                        <meta property="og:title" content="Tftdle" />
                        <meta property="og:description" content="TFTdle is a wordle like game for the popular game teamfight tactics! Try to guess the daily TFT champion!"/>
                        <meta property="og:image" content="https://www.tftdle.net/static/media/tftdlelogo.00fdb66ca7aa201e428f.png" />
                        <meta property="og:url" content="https://www.tftdle.net/Set85" />
                        <meta property="og:type" content="website" />
                        <meta name="twitter:title" content="Tftdle"/>
                        <meta name="twitter:description" content="TFTdle is a wordle like game for the popular game teamfight tactics! Try to guess the daily TFT champion!" />
                        <meta name="twitter:image" content="https://www.tftdle.net/static/media/tftdlelogo.00fdb66ca7aa201e428f.png"/>
                        <meta name="twitter:card" content="tftdle_logo" />
                    </Helmet>
                <div style={{
                    position: "fixed",
                    top: "0",
                    left: "0",
                    backgroundImage: `url(${background})`,
                    backgroundPosition: 'center',
                    backgroundSize: 'cover',
                    backgroundRepeat: 'no-repeat',
                    width: '100vw',
                    height: '100vh'
                }}>
                </div>
                <div className='App'
                    style={{
                        position: "relative"
                    }}>
                    <ChampionList set={85}></ChampionList>
                </div>
                <div style={{ position: "relative", margin: "10px" }}>
                <Suspense fallback={<div/>}>
                    <MyModal/>
                </Suspense>
                </div>
                {adEnabled && <Ad/>}
            </div>
        );
    }
}

export default Set85;