import React, { useEffect, useRef } from 'react';
import Countdown, { zeroPad } from 'react-countdown';
import dropdownIcon from "../img/tftdle-chooseyourchampion.webp";
import "../css/Common.css"
import { Button } from 'reactstrap';
import { Link } from 'react-router-dom';





function MyVictory({ answer, isMobile, timeUntilMidnight, withSkill, from, legendImg }) {

    const myRef = useRef(null)

    useEffect(() => {
        setTimeout(() => {
            if (myRef.current != null)
                myRef.current.scrollIntoView()
        }, 3000)


    }, [])

    const renderer = ({ hours, minutes, seconds }) => {

        return <span style={{ fontSize: "25px" }}>Time until next game <br /> {zeroPad(hours)}:{zeroPad(minutes)}:{zeroPad(seconds)}</span>;

    };

    const buttonStyle = {
        height: isMobile ? "45px" : "80px",
        marginBottom: "10px",
        width: "350px",
        position: "relative",
        backgroundImage: `url(${dropdownIcon})`,
        backgroundPosition: 'center center',
        backgroundSize: 'contain',
        backgroundRepeat: 'no-repeat',
        backgroundColor: "transparent",
        display: "inline-block",
        fontFamily: "Roboto",
        color: "white",
        fontSize: isMobile? "14px": "20px",
        textShadow: "0 0 3px black",
        border:"none",
        // textAlign: "left",
    }
    return <div style={{
        width: isMobile ? "360px" : "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(40, 170, 10, 0.8), rgb(117, 177, 64, 0.8))", border: "2px solid white", fontSize: "20px",
        fontFamily: "Roboto",
        color: "white",
        textShadow: "0 0 3px black",
        boxShadow: "inset 0 0 6px black"
    }} ref={myRef}>
        <div style={{
            margin: "10px"
        }}> 
        {from !== 'augment' && <><a title={answer.name}><img style={{ border: "solid 2px", borderRadius: "10px", boxShadow: "inset 0 0 6px black", margin:"auto"}} src={answer.imgUrl} alt="some" height="80px"></img></a>
        {withSkill && <img style={{ border: "solid 2px", borderRadius: "10px", boxShadow: "inset 0 0 6px black", margin: "5px" }} src={answer.skill} alt="some" height="80px"></img>}
        </>}

        {from === 'augment' && <><a title={answer.legend}><img style={{ border: "solid 2px", borderRadius: "50%", boxShadow: "inset 0 0 6px black", backgroundColor: "white", margin:"auto"}} src={require("../img/legendicons/legend_largecircle_"+ answer.legend.toLowerCase().replace(/ /g,'')+".png")} alt="some" height="80px"></img></a>
        </>}

        </div>
        {from !== 'augment' && <p>
            You have guessed <span style={{ fontWeight: "bold" }}>{answer.name}</span> correctly! {withSkill && <> This ability is called <span style={{ fontWeight: "bold" }}>{answer.skillName}</span>!</> }
        </p>}

        {from === 'augment' && <p>
            You have guessed <span style={{ fontWeight: "bold" }}>{answer.legend}</span> correctly!
        </p>}

        <hr style={{
            color: "white",
            backgroundColor: "white",
            height: "2px",
            // margin: "auto",
            // width: "50%"
        }} />

        <Countdown date={Date.now() + timeUntilMidnight} renderer={renderer} zeroPadTime={2}></Countdown>

        <hr />

        <div >
        {from === 'augment' && 
            <><p> Enjoy Tftdle augments? Try guessing a champion from TFT Set 8.5! </p> 
            <Link to="/set85"><Button className="scaleOnHover transforming-button" style={buttonStyle}> <span style={{ position: "relative", marginTop: "20px" }}>Guess the champion from Set 8.5!</span> </Button></Link></>
        }
        
            {from !== 'augment' && withSkill && <p> Enjoy Tftdle abilities? Try guessing the legend of an augment from TFT Set 9! </p> } 
            {from !== 'augment' && withSkill && <Link to="/augments"><Button className="scaleOnHover transforming-button" style={buttonStyle}> <span style={{ position: "relative", marginTop: "20px" }}>Guess the augment!</span> </Button></Link>} 
            {from !== 'augment' && !withSkill && <p> Enjoy Tftdle classic? Try guessing an ability of a TFT champion! </p> } 
            {from !== 'augment' && !withSkill && <Link to="/skills"><Button className="scaleOnHover transforming-button" style={buttonStyle}> <span style={{ position: "relative", marginTop: "20px" }}>Guess the ability!</span> </Button></Link>}
        </div>
        
    </div>

}

export default MyVictory;