import React, { Component } from 'react';
import secureLocalStorage from "react-secure-storage";
import Button from '@mui/material/Button';

class CookieController extends Component {
    constructor(props) {
        super(props);
        this.state = {
          cookiesOff: false
        };
        this.setCookieOff = this.setCookieOff.bind(this)
      }

    setWithExpiry(key, value, interval) {
        const now = new Date();
        const item = {
            value: value,
            expiry: now.getTime() + interval,
        };

        secureLocalStorage.setItem(key, JSON.stringify(item));
    }



    setCookieOff(){
        this.setWithExpiry("cookiesEnabled", false, 86400000)
        this.setState({cookiesOff: true})
    }

    render(){
        const {cookiesOff} = this.state
    return (
      <div >
        <Button  variant="outlined" onClick={this.setCookieOff}>OPT-OUT</Button>
        {cookiesOff && <p>You have opt-out of cookies for nect 24 hours!</p>}
      </div>
    );
    }

}
export default CookieController;