import React, { Component, lazy, useState } from 'react';

import { Tooltip } from 'react-tooltip'
import { Button, Dialog, DialogTitle, DialogContent, DialogActions, FormControlLabel, Switch, Slide, Typography } from '@mui/material';

import DifficultyIcon1 from "../img/icon-difficulty-1.webp";
import DifficultyIcon2 from "../img/icon-difficulty-2.webp";
import DifficultyIcon3 from "../img/icon-difficulty-3.webp";

function AbilityIcon({ answer, isMobile, numberOfGuesses, page }) {
    const [isBlackWhite, setIsBlackWhite] = useState(false);
    const [isRotated, setIsRotated] = useState(false);
    const [isPopUpOpen, setIsPopUpOpen] = useState(false);

    const handleToggleBlackWhite = () => {
        setIsBlackWhite(!isBlackWhite);
    };

    const handleToggleRotation = () => {
        setIsRotated(!isRotated);
    };

    const handleOpenPopUp = () => {
        setIsPopUpOpen(true);
    };

    const handleClosePopUp = () => {
        setIsPopUpOpen(false);
    };

    const difficulty = !isRotated + !isBlackWhite
    return (
        <div style={{ width: isMobile ? "360px" : "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(252, 204, 78, 0.8), rgb(249, 148, 65, 0.8))", border: "2px solid white", boxShadow: "inset 0 0 3px black" }}>
            {page == "ability" && <> <p style={{
                fontSize: "24px",
                fontFamily: "Roboto",
                color: "white",
                margin: "10px",
                textShadow: "0 0 3px black"
            }}>What champion has this skill?</p>
                <img
                    style={{
                        border: "white solid 2px",
                        borderRadius: "10px",
                        boxShadow: "inset 0 0 6px black",
                        margin: "10px",
                        filter: !isBlackWhite ? "grayscale(100%)" : "none",
                        transform: !isRotated ? "rotate(180deg)" : "none"
                    }}
                    src={answer.skill}
                    alt="some"
                    height="80px"
                /></>}
            {page == "augment" && <>
                <p style={{
                    fontSize: "24px",
                    fontFamily: "Roboto",
                    color: "white",
                    margin: "10px",
                    textShadow: "0 0 3px black"
                }}>What legend has this augment?</p>
                <img style={{
                    border: "white solid 2px", borderRadius: "10px", boxShadow: "inset 0 0 6px black", margin: "10px", filter: !isBlackWhite ? "grayscale(100%)" : "none",
                    transform: !isRotated ? "rotate(180deg)" : "none"
                }} src={answer.icon} alt="some" height="80px"></img>
                {3 - numberOfGuesses > 0 ? <p style={{
                    fontSize: "24px",
                    fontFamily: "Roboto",
                    color: "white",
                    margin: "10px",
                    textShadow: "0 0 3px black"
                }}>Augment name reveal in: <span style={{ color: "yellow" }}>{3 - numberOfGuesses} </span></p> : <p>{answer.augmentName} </p>}</>}

            <div style={{ textAlign: 'center', marginTop: '10px' }}>

                <button onClick={handleOpenPopUp}
                    id="Difficulty"
                    className="scaleOnHover"
                    style={{
                        height: "50px",
                        width: "50px",
                        backgroundImage: difficulty == 0 ? `url(${DifficultyIcon1})` : difficulty == 1 ? `url(${DifficultyIcon2})` : `url(${DifficultyIcon3})`,
                        backgroundPosition: 'center center',
                        backgroundColor: "transparent",
                        backgroundSize: isMobile ? 'contain' : 'contain',
                        backgroundRepeat: 'no-repeat',
                        padding: "0px",
                        margin: "0px",
                        border: "none",
                        outline: 'none',
                    }}
                    title='Info' >
                </button>
                <Tooltip
                    anchorId="Difficulty"
                    place="left"
                    content={difficulty == 0 ? `Difficulty: Easy` : difficulty == 1 ? `Difficulty: Medium` : `Difficulty: Hard`}
                />
            </div>

            <Dialog open={isPopUpOpen} onClose={handleClosePopUp} PaperProps={{ style: { background: "linear-gradient(to right, rgb(252, 204, 78, 0.8), rgb(249, 148, 65, 0.8))", border: "2px solid white", boxShadow: "inset 0 0 3px black" } }}>
                <DialogTitle>
                    <Typography variant="h6" style={{ color: "white", fontFamily: "Roboto" }}>Difficulty Settings</Typography>
                </DialogTitle>
                <DialogContent>
                    <FormControlLabel
                        control={<Switch checked={isBlackWhite} onChange={handleToggleBlackWhite} />}
                        label={<Typography style={{ color: "white", fontFamily: "Roboto" }}>Color</Typography>}
                    />
                    <FormControlLabel
                        control={<Switch checked={isRotated} onChange={handleToggleRotation} />}
                        label={<Typography style={{ color: "white", fontFamily: "Roboto" }}>Rotate</Typography>}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClosePopUp} style={{ color: "white", fontFamily: "Roboto" }}>Close</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default AbilityIcon;