import React, { useState } from 'react';
import "../css/Streak.css";
import { Tooltip } from 'react-tooltip'
import 'react-tooltip/dist/react-tooltip.css'
import streakImage from '../img/flameicon-textspace-nobar.webp'
import streakImage2 from '../img/flameicon-textspace-nobar-hot.webp'



function Streak({streak}) {
    return (
        <>
        <div className="icon-container scaleOnHover" id="streak" style={{
              filter: streak==0? "grayscale(100%)": "grayscale(0%)",
              backgroundImage: streak<3 ? `url(${streakImage})`:`url(${streakImage2})`
        }}>
            <span className="streak" >{streak}</span>
        </div>
        <Tooltip
        anchorId="streak"
        place="left"
        content="Streak"
      />
        </>)

}

export default Streak;