class AugmentList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            legends: [],
            legendsGuessed: [],
            answer: {},
            answerResult: false,
            hideComboBox: true,
            toggleInfoModal: false,
            numberOfPeopleSolved: 0,
            coloursGuessed: [],
            oldAnswer: {},
            date: 0,
            loading: true,
            isMobile: false,
            width: 0,
            height: 0,
            setNumber: 0,
            streak: 0,
        };
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }


    componentDidUpdate() {
        setWithExpiry('legendsGuessed', this.state.legendsGuessed, this.timeUntilMidnight())
        if (!this.isEmpty(this.state.answer) && !this.isEmpty(this.state.oldAnswer) && this.state.answer.augmentName !== this.state.oldAnswer.augmentName) {
            secureLocalStorage.clear()
            window.location.reload();
        }
    }

    componentDidMount() {


        var set = this.props.set;

        this.setState({ loading: true, setNumber: set })
        setTimeout(() => {
            this.setState({ loading: false });
        }, 500);

        var legendsStored = false
        try {
            this.getStateFromLocalStorage(set + 'answerResultA', 'answerResult')
            legendsStored = this.getStateFromLocalStorage(set + 'legends', 'legends')
            this.getStateFromLocalStorage('legendsGuessed', 'legendsGuessed')
            this.getStateFromLocalStorage(set + 'answerA', 'answer')
            this.getStateFromLocalStorage(set + 'hideComboBoxA', 'hideComboBox')
            this.getStateFromLocalStorage(set + 'streakA', 'streak')

        } catch (e) { }

        if (!legendsStored)
            fetch(`/legends`)
                .then(response => response.json())
                .then(data => this.setState({ legends: data }));

        fetch(`/augmentWinner`)
            .then(response => response.json())
            .then(data => this.setState({ answer: data }))


        fetch(`/solved?set=${encodeURIComponent(set)}&mode=augment`)
            .then(response => response.json())
            .then(data => this.setState({ numberOfPeopleSolved: data }))

        fetch(`/date?set=${encodeURIComponent(set)}`)
            .then(response => response.json())
            .then(data => this.setState({ date: data }))

        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);


    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    getStateFromLocalStorage = (key, stateName) => {
        const state = getWithExpiry(key);
        if (state != null) {
            this.setState({ [stateName]: state });
            if (stateName == 'answer') {
                this.setState({ oldAnswer: state })
            }
            return true
        }
        return false
    }

    isEmpty(obj) {
        for (const prop in obj) {
            if (Object.hasOwn(obj, prop)) {
                return false;
            }
        }

        return true;
    }

    isValidChoice = (selectedOptions) => {
        if (selectedOptions == null)
            return false;
        const { legendsGuessed } = this.state
        if (Object.keys(selectedOptions).length > 0) {
            if (legendsGuessed.length == 0) {
                return true;
            }
            for (var champ of legendsGuessed) {
                if (champ.name == selectedOptions.name)
                    return false
            }
            return true;
        }
        return false;
    }
    handleSubmit = (selectedOptions) => {

        if (this.isValidChoice(selectedOptions) && Object.keys(selectedOptions).length > 0) {
            var array1 = [...this.state.legendsGuessed];
            array1.unshift(selectedOptions)
            this.setState(prevState => ({
                legendsGuessed: [selectedOptions, ...prevState.legendsGuessed]
            }))


            if (this.isCorrectAnswer(selectedOptions)) {
                this.setState({ answerResult: this.state.answerResult = true })
            }
            var array = [...this.state.legends];
            var index = array.indexOf(selectedOptions)
            if (index !== -1) {
                array.splice(index, 1);
                this.setState({ legends: array });
                setWithExpiry(this.state.setNumber + 'legends', array, this.timeUntilMidnight())
            }

        }
    }

    toggleInfoModal = () => {
        this.setState({ toggleInfoModal: !this.toggleInfoModal })
    }

    textBoxHandle = () => {
        this.setState({
            hideComboBox: false
        })
        setWithExpiry(this.state.setNumber + "hideComboBoxA", false, this.timeUntilMidnight())
    }


    timeUntilMidnight() {
        var midnight = new Date();
        midnight.setUTCHours(24, 0, 0, 0);
        return (midnight.getTime() - new Date().getTime());
    }

    isCorrectAnswer = (guess) => {
        const streak = this.state.streak
        setWithExpiry(this.state.setNumber + 'answerA', this.state.answer, this.timeUntilMidnight())

        var count = 0;
        if (this.state.answer.legend == guess.name) {
            count++;
        }


        if (count == 1) {
            setTimeout(() => {
                this.textBoxHandle()
            }, 1000)
            setWithExpiry(this.state.setNumber + 'answerResultA', true, this.timeUntilMidnight())
            if (this.answerResult != true) {
                setWithExpiry(this.state.setNumber + 'streakA', streak + 1, this.timeUntilMidnight() + 86400000)
                this.setState(prevState => {
                    return { streak: prevState.streak + 1 }
                })
                var set = this.props.set;
                fetch(`/solve?set=${encodeURIComponent(set)}&mode=augment`)
                    .then(response => response.json())
                setTimeout(() => {
                    fetch(`/solved?set=${encodeURIComponent(set)}&mode=augment`)
                        .then(response => response.json())
                        .then(data => this.setState({ numberOfPeopleSolved: data }))
                }, 1000)

            }
            return true;
        }
        return false;
    }
    getGuessColour = (guess, answer) => {
        const green = "#28aa0a"
        const red = "#D4312B"
        const orange = "#f07e0f"
        if (guess.name === answer.legend)
            return green;
        return red;
    }

    render() {
        const { legends: legends, legendsGuessed, answer, answerResult, hideComboBox, numberOfPeopleSolved, coloursGuessed, date, loading, width, setNumber, streak } = this.state;
        const isMobile = isMobileWidth(width)

        const legendList = legendsGuessed.map(legend => {

            const guessColour = this.getGuessColour(legend, answer);
            return <tr key={legend.name}>

                <Slide direction="down" in={true} timeout={{ enter: 1000, exit: 250 }} mountOnEnter unmountOnExit>

                    <td style={{ border: "1px solid rgb(255, 255, 255, 0)", margin: "0px", padding: "0px" }} >
                        <div style={{ backgroundColor: guessColour, width: "150px", margin: "auto", height: "100px", padding: "auto", border: "solid 1px", borderRadius: "5px", boxShadow: "inset 0 0 6px black" }}>
                            <a title={legend.name}><img style={{ margin: "10px" }} src={require(`${legend.imgUrl}`)} alt={legend.name} height="80px"></img></a>
                        </div>
                    </td>
                </Slide>

            </tr>
        });

        return (
            <div>
                <AppNavbar isMobile={isMobile} />
                {loading ? (
                    <div style={{ width: isMobile ? "355px" : "650px", margin: "auto" }} >
                        <ColorRing
                            visible={true}
                            height="80"
                            width="80"
                            ariaLabel="blocks-loading"
                            wrapperStyle={{}}
                            wrapperClass="blocks-wrapper"
                            colors={['#e15b64', '#f47e60', '#f8b26a', '#fccc4e', '#f99441']}
                        />
                    </div>
                ) : (
                    <div>
                        <div className={(answerResult ? 'show' : 'hidden')}>
                            {answerResult && <MyConfetti isAnswered={answerResult} delay={1000} handler={this.textBoxHandle} />}
                        </div>
                        <div style={{
                            height: "120px",
                        }}>
                            {hideComboBox && <ComboBox isMobile={isMobile} champions={legends} handleSubmit={this.handleSubmit} isLegend={true} />}
                            {answerResult && <MyEmote delay={1000} isAnswered={answerResult} />}
                        </div>
                        <div style={{
                            marginBottom: "10px",
                        }}>
                            <AbilityIcon answer={answer} isMobile={isMobile} numberOfGuesses={legendsGuessed != undefined ? legendsGuessed.length : 3} page={"augment"}></AbilityIcon>
                        </div>
                        <div className='pad' style={{
                            width: "360px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(249, 148, 65, 0.8), rgb(252, 204, 78, 0.8))", border: "2px solid white", position: "relative"

                        }}>
                            {!isMobile && <img src={headersImage} style={{
                                width: "350px",
                                height: "50px",
                                marginLeft: "-175px",
                                marginTop: "0px",
                                padding: "0px",
                                position: "absolute",
                                zIndex: "1"
                            }}></img>}
                            <Table className="align-middle"
                                borderless
                                // hover
                                responsive
                                style={{
                                    position: "relative",
                                    borderCollapse: "separate",
                                    borderSpacing: "5px 5px",
                                    fontFamily: "Roboto",
                                    color: "white",
                                    width: "100%",
                                    textShadow: "0 0 3px black",
                                    zIndex: "2"
                                }}
                            >
                                <thead>
                                    <tr style={{ zIndex: "4" }}>
                                        <th style={{ width: "50%" }}>Champion</th>
                                    </tr>
                                </thead>
                                <tbody >
                                    {legendList}
                                </tbody>
                            </Table>
                        </div>

                        <div
                            style={{
                                fontSize: "20px",
                                fontFamily: "Roboto",
                                color: "white",
                                width: "100%",
                                textShadow: "0 0 3px black"
                            }}>
                            <Solved numberOfPeopleSolved={numberOfPeopleSolved} hideComboBox={hideComboBox} date={date}></Solved>
                        </div>
                        <Streak streak={streak}></Streak>
                        <div style={{
                            marginBottom: "10px"
                        }}>
                            {!hideComboBox && <MyVictory autoFocus coloursGuessed={coloursGuessed} championsGuessed={legendsGuessed} getGuessColour={this.getGuessColour} answer={answer} date={date} isMobile={isMobile} timeUntilMidnight={this.timeUntilMidnight()} withSkill={true} from={"augment"} legendImg={legendsGuessed[0].imgUrl}></MyVictory>}
                        </div>
                    </div>
                )}
            </div>
        );
    }
}


export default AugmentList;